<h2>Instance Properties</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>type</th>
<th>default</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>url</td>
<td>String|Object</td>
<td></td>
<td><code v-pre>required</code>The URL template to use to request tiles.</td>
</tr>
<tr>
<td>pickFeaturesUrl</td>
<td>String|Object</td>
<td></td>
<td><code v-pre>optional</code>The URL template to use to pick features.</td>
</tr>
<tr>
<td>urlSchemeZeroPadding</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code>Gets the URL scheme zero padding for each tile coordinate.</td>
</tr>
<tr>
<td>subdomains</td>
<td>String|Array</td>
<td><code v-pre>'abc'</code></td>
<td><code v-pre>optional</code>he subdomains to use for the {s} placeholder in the URL template. If this parameter is a single string, each character in the string is a subdomain. If it is an array, each element in the array is a subdomain.</td>
</tr>
<tr>
<td>credit</td>
<td>String|Object</td>
<td><code v-pre>''</code></td>
<td><code v-pre>optional</code>A credit for the data source, which is displayed on the canvas.</td>
</tr>
<tr>
<td>minimumLevel</td>
<td>Number</td>
<td><code v-pre>0</code></td>
<td><code v-pre>optional</code>The minimum level-of-detail supported by the imagery provider. Take care when specifying this that the number of tiles at the minimum level is small, such as four or less. A larger number is likely to result in rendering problems.</td>
</tr>
<tr>
<td>maximumLevel</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code>The maximum level-of-detail supported by the imagery provider, or undefined if there is no limit.</td>
</tr>
<tr>
<td>rectangle</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code>The rectangle, in radians, covered by the image. <strong>structure: { west: number, south: number, east: number, north: number }</strong></td>
</tr>
<tr>
<td>tilingScheme</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code>The tiling scheme specifying how the ellipsoidal surface is broken into tiles. If this parameter is not provided, a WebMercatorTilingScheme is used.</td>
</tr>
<tr>
<td>ellipsoid</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code>The ellipsoid. If the tilingScheme is specified, this parameter is ignored and the tiling scheme's ellipsoid is used instead. If neither parameter is specified, the WGS84 ellipsoid is used.</td>
</tr>
<tr>
<td>tileWidth</td>
<td>Number</td>
<td><code v-pre>256</code></td>
<td><code v-pre>optional</code>Pixel width of image tiles.</td>
</tr>
<tr>
<td>tileHeight</td>
<td>Number</td>
<td><code v-pre>256</code></td>
<td><code v-pre>optional</code>Pixel height of image tiles.</td>
</tr>
<tr>
<td>hasAlphaChannel</td>
<td>Boolean</td>
<td><code v-pre>true</code></td>
<td><code v-pre>optional</code>true if the images provided by this imagery provider include an alpha channel; otherwise, false. If this property is false, an alpha channel, if present, will be ignored. If this property is true, any images without an alpha channel will be treated as if their alpha is 1.0 everywhere. When this property is false, memory usage and texture upload time are potentially reduced.</td>
</tr>
<tr>
<td>getFeatureInfoFormats</td>
<td>Array</td>
<td></td>
<td><code v-pre>optional</code>The formats in which to get feature information at a specific location when UrlTemplateImageryProvider#pickFeatures is invoked. If this parameter is not specified, feature picking is disabled.</td>
</tr>
<tr>
<td>enablePickFeatures</td>
<td>Boolean</td>
<td><code v-pre>true</code></td>
<td><code v-pre>optional</code>If true, UrlTemplateImageryProvider#pickFeatures will request the options.pickFeaturesUrl and attempt to interpret the features included in the response. If false, UrlTemplateImageryProvider#pickFeatures will immediately return undefined (indicating no pickable features) without communicating with the server. Set this property to false if you know your data source does not support picking features or if you don't want this provider's features to be pickable. Note that this can be dynamically overridden by modifying the UriTemplateImageryProvider#enablePickFeatures property.</td>
</tr>
<tr>
<td>customTags</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code>Allow to replace custom keywords in the URL template. The object must have strings as keys and functions as values.</td>
</tr>
</tbody>
</table>
<hr>
<ul>
<li>Refer to the official document: <strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/UrlTemplateImageryProvider.html">UrlTemplateImageryProvider</a></strong></li>
</ul>
<h2>Events</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>parameter</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>Triggers when the component is ready. It returns a core class of Cesium, a viewer instance, and the cesiumObject.</td>
</tr>
<tr>
<td>errorEvent</td>
<td>TileProviderError</td>
<td>Triggers when the imagery provider encounters an asynchronous error.</td>
</tr>
<tr>
<td>readyPromise</td>
<td>ImageryProvider</td>
<td>Triggers when the provider is ready for use.</td>
</tr>
</tbody>
</table>
